.nav-content{
    background-color: #1C2B35;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 50px;
}

.nav-content div a{
    margin-left: 16px;
    color:#FFFFFF;
    text-decoration: none;
    font-size: 17px;
    letter-spacing: 0.5%;
    
    
}

.nav-content div a:hover{
    color: orange;
}